.checkbox input {
  width: 1.5rem;
  height: 1.5rem;
}

div[id="skills-input-block"],
div[id="about-input-block"],
div[id="post-input-block"] {
  height: auto;
}

.formErrors {
  margin: 2rem 0;
}

.label {
  color: var(--icon-color);
}

.item {
  display: flex;
  border-spacing: 2rem;
  flex-direction: row;
  align-items: center;
  gap: 2rem;
}

.item .input {
  width: 70%;
}

.button {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  margin-top: 1rem;
}

.button button {
  width: 10rem;
}

.contacts {
  display: flex;
  flex-direction: column;
}

@media (max-width: 768px) and (min-width: 576px) {
  .item {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    padding: 1rem 0;
    gap: 0.7rem;
  }

  .item div {
    width: 100% !important;
  }

  .checkbox {
    margin-left: 1rem;
  }
}
